.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  color: #fff;
}

.flex-container {
  display: flex;
}

.flex-child {
  flex: 1;
}

.flex-child:first-child {
  margin-right: 20px;
}

.footer {
  bottom: 0;
  width: 100%;
  height: 50px;
  background-color: #141a20;
  text-align: center;
  color: white;
}

.navbar-right {
  text-align: center;
}

.div-mainImage {
  padding-top: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
  max-width: 900px;
  height: auto;
  width: auto;
}

.div-contactImage {
  padding-top: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
  max-width: 450px;
  height: auto;
  width: auto;
}

p {
  margin-top: -20px;
}

.divUpload {
  padding-top: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
  text-align: center;
}
.navbar-right {
  padding-right: 20px;
  color: grey;
}

.image {
  flex: 1;
  width: 400px;
  height: 300px;
}
